import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FolienListComponent } from './components/folien-list/folien-list.component';

import { StaticpagesComponent } from './components/static-pages/staticpages.component';

const routes: Routes = [
  {
    path: 'folien',
    component: FolienListComponent,
  },
  {
    path: ':id',
    component: StaticpagesComponent,
    data: {
      markdown: true,
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class StaticpagesRoutingModule {}
